	module.exports = {"data":{"site":{"siteMetadata":{"title":"momorandum","subtitle":"momo's memorandums","copyright":"© All rights reserved.","author":{"name":"momo","twitter":"sbg"},"disqusShortname":"","url":"https://m.bug.org"}},"markdownRemark":{"id":"/Users/momo/web/gatsby-momo/src/pages/articles/2011-11-07-ubuntu-server/index.md absPath of file >>> MarkdownRemark","html":"<p>Ubuntu Server を 11.10 に upgrade した．\n基本的に手順どおりにやればはまりどころは少ないと思う．\nが，VMware ゲストとして動かしている場合，reboot 後にはまるポイントがあるので注意．</p>\n<ul>\n<li><a href=\"https://wiki.ubuntulinux.jp/UbuntuTips/Install/UpgradeOneiric\">Ubuntu 11.10 へアップグレードを行うには - Ubuntu Japanese Wiki</a></li>\n</ul>\n<p>VMware Server 上で動かしていた VM では下記の問題を踏んだ．\nネットワークが繋がらなくなるので，コンソールをとらないと解消できない．\nESXi 上の VM は大丈夫だったなぁ．</p>\n<ul>\n<li><a href=\"http://www.totalcomputersusa.com/2011/10/ubuntu-11-10-booting-system-without-full-network-configuration/\">Ubuntu 11.10 “Booting system without full network configuration” | Total Computers USA</a></li>\n</ul>","fields":{"tagSlugs":["/tags/linux/","/tags/ubuntu/"]},"frontmatter":{"title":"Ubuntu Server 11.10 upgrade","tags":["Linux","Ubuntu"],"date":"2011-11-07T20:24:00.000Z","description":"Ubuntu Server を 11.10 に upgrade した．"}}},"pathContext":{"slug":"/posts/2011110700/"}}

